
/* Custom styles */

.bg-theme-color {
  background: #ed0b7b !important;
}
.text-theme-color {
  color: #ed0b7b !important;
}
.text-fill-theme-color {
  -webkit-text-stroke: 2px #5c0b33 !important;
  -webkit-text-fill-color: transparent;
}
.menu-item.active .menu-link, .menu-item.active .menu-link .dropdown-icon {
  color: #ed0b7b !important;
}
.main-menu .menu-list .menu-item .menu-link:hover {
  color: #ed0b7b !important;
}
.main-menu .menu-list .menu-item .submenu .sub-item a::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0;
  background: linear-gradient(90deg, #ed0b7b00, #cf24357d, #ed0b7b4f);
  width: 100%;
  height: 1px;
  transform: scaleX(0);
  transform-origin: left;
  transition: 0.4s ease-in;
  z-index: -1;
}
.about-card .CircularProgressbar-path {
  stroke-width: 2px;
  stroke: #ed0b7b !important;
}
.banner-content h1 {
  font-size: 3rem !important;
  line-height: 1.2;
}


/* Mobile */
@media all and (max-width:991px){
  header {
      padding: 10px 15px !important;
  }
  .main-menu {
      background-color: #151515 !important;
      max-width: unset !important;
  }
  .mobile-logo-wrap {
      justify-content: left !important;
  }
}
